        "Ciljevi kolegija": "Ciljevi kolegija su: stei sposobnost prepoznavanja i opisivanja filozofskih pitanja u znanstvenoj spoznaji",
        "Ishodi uenja (ope i specifine kompetencije)": "Nakon poloenog kolegija student e znati/moi: napraviti logiku analizu znanstvenih tekstova.",
        "Sadraj silabusa/izvedbenog plana (ukratko)": " Kolegij obuhvaa tri dijela. Prvi je dio uvodnog karaktera i posveen je openitom filozofsko-logikom i metodolokom obrazovanju. Drugi i trei usmjereni su prema izabranim pitanjima filozofije znanosti. U prvom se dijelu uvode osnovni logiki i metodoloki pojmovi. U drugom dijelu razmatraju se izabrani problemi filozofije znanosti uz tumaenje izvornih tekstova. Odnos spoznaje i jezika tematizira se na Wittgensteinovom Tractatus-u. Prouava se Carnapov (empirijski) kriterij smislenosti. Analizira se Popperov prikaz znanstvene metode. Razlike izmeu znanosti o ovjeku i znanosti o prirodi analiziraju se obzirom na razliku u vrsti objanjenja (Von Wright) i obzirom na jezik (Davidson). Pitanje ogranienja spoznaje istrauje se u filozofskom tumaenju Gdelovih metateorijskih rezultata. U treem, kraem dijelu analizira se pitanje promjene teorije u okviru Kuhnove filozofije povijesti znanosti te u okviru suvremenih formalnih teorija.",
