        "Ciljevi kolegija": "Kolegij je zamiljen kao programerski kolegij uvodne razine za studente sa prijanjim iskustvom programiranja. U sklopu kolegija, studentima koji su upoznati sa proceduralnom paradigmom, predstavljaju se koncepti objektno orijentiranog programiranja. Kolegij zapoinje sa kratkim pregledom upravljakih struktura i podatkovnih tipova sa naglaskom na strukturirane tipove podataka i rad sa nizovima. Zatim se nastavlja sa prikazom objektno orijentirane paradigme, pri emu je fokus na definiciji i nainu koritenja klasa, zajedno sa osnovama objektno orijentiranog razvoja. Na kraju kolegija, oekuje se da studenti usvojene koncepte demonstriraju kroz izradu jednostavne dvodimenzionalne raunalne igre u odgovarajuem okviru koji e im biti predstavljen za vrijeme kolegija.",
        "Ishodi uenja (ope i specifine kompetencije)":
`Nakon poloenog kolegija student e znati/moi:
- Razviti jednostavan objektno orijentirani (OO) projekt koristeiOO paradigmu i pripadajue pomone alate.
- Implementirati OO model u OO jeziku visoke razine koritenjem objekata, klasa, nasljeivanja, nizova, uvjetovanih izraza i iteracije. Upoznati sa nainom dokumentiranja, rasporedom, testiranjem i pronalaenjem greaka kod OO programiranja.
- Objasniti prednosti koritenja OO razvojnog pristupa i u kojim sluajevima je to prikladna metodologija.
- Primijeniti ispravnu programersku paradigmu ovisno o zadanom problemu, te biti upoznat sa utjecajem odabrane paradigme na razvoj i odravanje aplikacija.
- Dizajnirati i implementirati prikladno GUI (grafiko korisniko suelje) za pristupni (front-end) dio objektno orijentirane aplikacije.
        `,
