        "Ciljevi kolegija": "Umjetna inteligencija (UI) je podruje koje je posveeno prouavanju raunalnog modela inteligentnog ponaanja. Zajedniko svim podrujima umjetne inteligencije je izrada agenata ili strojeva koji imaju odlike inteligentnog ponaanja; rjeavanje problema, predstavljanje znanja, zakljuivanje, uenje, percepcija i interpretiranje. Koliina razliitog gradiva na kolegiju odraava raznolikosti navedenih pojmova. Tijekom kolegija, osvrnut emo se na temeljna pitanja i problematiku u podruju UI te istraiti temeljne tehnike navedenog podruja. Kolegij je projektno orijentiran, s praktinim zadacima koji se rjeavaju tijekom cijelog semestra, koristei NetLogo programsko okruenje utemeljeno na LISP i Prolog programskim jezicima.",
