        "Sadraj predmeta": `Sigurnosne prijetnje umreenim raunalima. Osnovne tehnike obrane. Obrana lokalne mree. Koncept vatrozida. Razine filtriranja. Podrka za privatne i dinamike adrese. Demilitarizirana zona. Detekcija napada, sustavi i mreni sustavi za detekciju napada. Analiza mrenog prometa, alati i metode, etherreal, tcpdump, snort. Odravanje i podeavanje pravila i razina uzbune, analiza rezultata, integracija u sustav, pravila primjene, mogue greke. Prevencija napada. Povezivanje na centralni nadzorni sustav. Rukovanje incidentima, priprema i kontrola incidenata. Tipovi organizacije incidentnog tima, raspoloivost timova i resursa. Meunarodna organizacija za rukovanje incidentima, CERT, Planiranje i provoenje programa za rukovanje incidentima. Infrastruktura za rukovanje incidentima, dojave kontrola incidenata. Suradnja sa drugim organizacijama u rjeavanju incidenta. Alati i postupci za pojaavanje sigurnosti sustava, tcp wrapperi, kontrola servisa, otisak sustava. Pojaavanje sigurnosti aplikacija. Odnos sigurnosti i pouzdanosti sustava politike sigurnosti.`,
