        "Ciljevi predmeta":
`Upoznati studente s osnovnim pojmovima i temama iz Filozofije odgoja i obrazovanja.
Kritiki analizirati socioloke perspektive i relevantnu literaturu o obrazovanju i odgoju.
Analizirati odgoj i obrazovanje u sloenoj strukturi ljudskog drutva, s posebnim naglaskom na suvremena drutva.        
`,
        "Uvjeti za upis predmeta": "Nema uvjeta",
        "Ishodi uenja (kompetencije)": 
`Studenti e na kraju ovog kolegija moi:
i. Analizirati vanost odgoja i obrazovanja za razvoj ljudskog drutva;
ii. objasniti utjecaj drutva na odgoj i obrazovanje, i obrnutom sluaju utjecaj odgoja i obrazovanja na drutvo;
iii. predoiti smjerove u kojima bi obrazovanje moglo krenuti u budunosti;
iv. analizirati pojam inteligencije kao socioloke kategorije, s posebnim naglaskom na teoriju inteligencije Karla Manheima;
v. objasniti pojam obrazovanja kroz razliita filozofska odreenja.        
`,
        "Sadraj predmeta": "Kroz ovaj kolegij govorit e se o glavnim problemima filozofije obrazovanja, a potom se obrauju i osnovni pravci sociologije obrazovanja, funkcionalno i intencionalno obrazovanje, a analizira se i jednakost obrazovnih ansi. Daju se predodbe o moguem buduem razvoju obrazovanja, a analizira se pojam inteligencije kao socioloke kategorije i daje se joj se mogue socioloko odreenje, a posebno se obrauje teorija inteligencije Karla Manheima. Nadalje se obrauje obrazovanje kroz socioloke perspektive: liberalnu, socijaldemokratsku, konfliktnu, postmodernu, interakcionistiku, a i osvre se na mogue promijene u obrazovanju, te daje kritika evaluacija obrazovnih reformi, a obrauje se i koliko etnika pripadnost pridonosi obrazovnom postignuu, te koji su elementi potpore obrazovanju.",
