        "ECTS bodovi": "5",
        "Ciljevi predmeta": "Razvijati sposobnost studenata da ovladaju osnovnim znanjima o upravljanju znanjem i ostvarivanju konkurentske prednosti, ime studenti stjeu znanja i osnovne vjetine o informacijskim i drugim sustavima za potporu u upravljanju znanjem, a u svrhu ostvarivanja temelja na kojima e razvijati specijalizirana znanja potrebna za ostvarivanje realne (trine i dr.) konkurentske prednosti (u poslovanju i dr.) tvrtki (te drugih organizacija i institucija). Studenti e upoznati: pojmove znanja i upravljanja znanjem, kljune faktore uspjenosti poslovanja suvremene tvrtke, strategije razvoja i upravljanja tvrtkom i njenim znanjem, uspjeno implementirane koncepte i modele upravljanja znanjem vodeih tvrtki, kritine faktore uspjeha za ostvarivanje konkurentske prednosti (uz razne strategije upravljanja znanjem, kulture tvrtki i kljune informacijskokomunikacijske i dr. tehnologije, pristupe i aspekte) koje e im pomoi da ovladaju i postupno primjenjuju nove modele upravljanja tvrtkom i njenim znanjem za ostvarivanje konkurentske prednosti. Jednako tako nastoji se studente upoznati s temeljnim modelima i aplikacijama kojima se mogu istraivati, kreirati i kontrolirati kritini faktori uspjeha za ostvarivanje konkurentske prednosti kod upravljanja znanjem (uz razne strategije upravljanja znanjem, kulture tvrtki i kljune informacijsko-komunikacijske i dr. tehnologije, pristupe i aspekte)..",
        "Uvjeti za upis predmeta": "Naelno nema",
        "Ishodi uenja (kompetencije)":
`Studenti trebaju biti u stanju:

Definirati i objasniti to naprednije koncepte: znanja i upravljanja znanjem (kao: teorija upravljanja znanjem: definicija i vrste znanja, proces upravljanja znanjem), kljune faktore uspjenosti poslovanja suvremene tvrtke, strategije razvoja i upravljanja tvrtkom i njenim znanjem, uspjeno implementirane koncepte i modele upravljanja znanjem vodeih tvrtki, kritine faktore uspjeha za ostvarivanje konkurentske prednosti (uz razne strategije upravljanja znanjem, kulture tvrtki i kljune informacijskokomunikacijske i dr. tehnologije, pristupe i aspekte). Znati razlikovati i povezivati znanja i upravljanja znanjem u opsegu vezanom za ostvarivanje konkurentske prednosti. Objasniti koncepte predvidivih trendova razvoja kljunih faktora uspjenosti poslovanja suvremene tvrtke.

Definirati i objasniti sve bitne koncepte i aspekte strategije razvoja i upravljanja tvrtkom i njenim znanjem (kao: upravljanje znanjem u gospodarstvu: preduvjeti uspjenog upravljanja znanjem; koncept upravljanja znanjem; procjena spremnosti tvrtke na upravljanje znanjem; mogui problemi prilikom uvoenja koncepta upravljanja znanjem u tvrtki; kriteriji za odabir adekvatnog pristupa upravljanju znanjem; metode upravljanja znanjem). Znati razlikovati dobre i loe strane uspjeno implementiranih koncepta i modela upravljanja znanjem vodeih tvrtki, kritine faktore uspjeha za ostvarivanje konkurentske prednosti (uz razne strategije upravljanja znanjem, kulture tvrtki i kljune informacijskokomunikacijske i dr. tehnologije, pristupe i aspekte) danas i u predvidivoj budunosti.

Znati to naprednije ovladati i upravljati temeljnim modelima i aplikacijama kojima se mogu istraivati, kreirati ikontrolirati kritini faktori uspjeha za ostvarivanje konkurentske prednosti kod upravljanja znanjem (uz razne strategije upravljanja znanjem, kulture tvrtki i kljune informacijsko-komunikacijske i dr. tehnologije, pristupe i aspekte), te napredno koristiti: razne strategije upravljanja znanjem, najvanije aspekte kulture tvrtki, kljune informacijsko-komunikacijske i dr. tehnologije, pristupe i aspekte (kao: mogua istraivanja upravljanja znanjem i povezanosti sa ostvarivanjem konkurentske prednosti: hipoteze i ciljevi te metode istraivanja; sudionici i mjerni instrument - varijable; razlike u metodama upravljanja znanjem i povezanosti izmeu skupina varijabli istraivanja).

Znati koristiti to naprednije kljune informacijsko komunikacijske i dr. tehnologije za upravljanje znanjem, kao to su metode upravljanja znanjem: analiza socijalnih mrea - SNA; Intranet i sl.; baze znanja; agente; zajednice znanja, interesa i prakse; trening - coaching i mentorstvo; nauene lekcije - Lessons learned; sustav za upravljanje dokumentima - Document Management; plave stranice - Blue Pages.

Znati koristiti to naprednije ostale kljune tehnologije, pristupe i aspekte za upravljanje znanjem, kao to su metode upravljanja znanjem: koritenje znanja klijenata; sustavi bazirani na umjetnoj inteligenciji; sustavi za poslovnu inteligenciju; naini motiviranja zaposlenika na dijeljenje znanja; dr.
`,
